.updateProfileContainer {
  width: 100vw;
  max-width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgb(241, 241, 241);

  /* position: fixed;
    top:0%;
    left:0; */
}
.updateProfileBox {
  background-color: #fff;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.219);
  width: 25vw;
  height: 60vh;
  text-align: center;
  padding-top: 7px;
  overflow: hidden;
}
.updateProfileBox>h3{
  color:rgb(97, 96, 96);
  font-size: 1.2rem;
}
.updateProfileBox > div > button {
  height: 3px;
  width: 50%;
  background-color: tomato;
  border: none;
  transform: all 0.3s;
}

.updateProfileForm {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  height: 80%;
  padding: 2vmax;
  transition: all 0.3s;
  z-index: 1000 !important;
}

.updateProfileForm > div {
  width: 100%;
  display: flex;
  align-items: center;
}
.updateProfileForm > div > input {
  width: 100%;
  border: none;
  border: 1px solid rgba(0, 0, 0, 0.267);
  padding: 1vmax 1vmax 1vmax 4vmax;
  border-radius: 4px;
  font: 300 1vmax cursive;
}
.updateProfileForm > div >svg{
    position: absolute;
    font-size: 1.5vmax;
    transform: translateX(1vmax);
    color: rgba(0, 0, 0, 0.623);
}
.updateProfileForm > div > {
  position: absolute;
  font-size: 1.5vmax;
  transform: translateX(1vmax);
  color: rgba(0, 0, 0, 0.623);
}
.updateProfileForm > div > input:focus {
  outline: none;
}
.updateProfileForm > a {
  color: rgba(0, 0, 0, 0.651);
  font: 500 0.8vmax "Gill Sans";
  align-self: flex-end;
}
.updateProfileForm > a:hover {
  color: #000;
}
.updateProfileBtn {
  font: 500 1.1vmax "Roboto";
  width: 100%;
  border: none;
  padding: 1vmax;
  background-color: tomato;
  color: #fff;
  cursor: pointer;
  transition: all 0.3s;
  border-radius: 4px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.219);
}
.updateProfileBtn:hover {
  background-color: rgb(179, 66, 46);
}

#updateProfileImage > img {
  width: 2.5vmax;
  border-radius: 100%;
  margin-left: 0.8vmax;
  position: absolute;
}
#updateProfileImage > input::-webkit-file-upload-button {
  cursor: pointer;
  /* width:100%; */
  background-color: #d7e1e6;
  z-index: 10;
  height: 4vh;
  border: none;
  padding: 0 1vmax;
  border-radius: 4px;
  font: 300 1vmax cursive;
  transition: all 0.3s;
  color: rgba(0, 0, 0, 0.623);
}
#updateProfileImage > input::-webkit-file-upload-button:hover {
  background-color: #b8dcee;
  color: #000;
}
